/* Fondo de la página */
body {
  background-color: #f3f4f6;
  font-family: 'Arial', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

/* Estilos de la calculadora */
.calculator {
  width: 400px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  background: linear-gradient(to bottom, #f9f9f9, #e2e2e2);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Display de la calculadora */
#display {
  width: 100%;
  height: 100px;
  background-color: #282c34;
  color: white;
  text-align: right;
  padding: 10px;
  font-size: 2.5em;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
  overflow: hidden;
}

/* Estilos de los botones */
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
}

.buttons button {
  background-color: #4CAF50;
  color: white;
  font-size: 1.5em;
  padding: 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.buttons button:hover {
  background-color: #45a049;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
}

.buttons button:active {
  background-color: #3e8e41;
}

/* Estilos para el botón Clear */
#clear {
  background-color: #ff5733;
}

#clear:hover {
  background-color: #e24d28;
}

#clear:active {
  background-color: #c94c22;
}

/* Estilo de los números */
button:focus {
  outline: none;
}

/* Ajustes responsivos */
@media (max-width: 400px) {
  .calculator {
    width: 100%;
    padding: 10px;
  }

  #display {
    font-size: 2em;
  }

  .buttons button {
    font-size: 1.2em;
    padding: 15px;
  }
}
